.card-grid-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  padding: 16px;
  position: fixed;
}

.heading {
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
  margin-left: 10px;
  margin-top: -6px;
  height: auto;
  font-weight: 500;
  color: rgba(62, 62, 62, 1);
}

.heading h1 {
  font-size: 16px;
  margin: 0;
  
}

.heading p {
  font-size: 16px;
  margin: 0;
  color: #3E3E3E;
  
}
.status-text{
  font-weight: bold;
  margin-left: 5px;
  color: #000000;
}

.pie {
  display: flex;
  align-items: flex-start;
  margin-top: -60px;
  margin-left: 130px;
  position: relative;
  pointer-events: none;
}

.bar-chart-container {
  background-color: rgba(239, 237, 237, 1);
  /* width: 100%; */
  padding: 5px;
  /* height: 90px; */
  /* margin-top: -50px; */
  display: grid;
  /* padding-left: 10px;
  padding-right: 10px; */
  grid-template-columns: repeat(2, 1fr);
  color: #3E3E3E;
}

.bar-chart-container div {
  /* padding-left: 10px; */
  margin-top: 2px;
}

.drop-icon {
  display: flex;
  width: '100%';
  /* margin-top: 15px; */
  margin-left: 180px;
  color: black;
  cursor: pointer;
}

/* .drop-icon svg {
  margin-top: -20px;
} */

.refresh-icon {
  display: flex;
  margin-top: 10px;
  justify-content: flex-end;
  cursor:pointer;
  padding-right: 10px;
}

.card-container {
  /* width: 300px; */
  height: 230px;
  border: 3px solid #D9D9D9;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 10px;
  margin: 0;
  background: white;
  z-index: 1;
  position: relative; /* Add position relative to allow positioning pseudo-elements */
  overflow: hidden;
}

.card-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 8px; 
  background-color: rgba(39, 174, 96, 1); 
  z-index: 2;
}

.running::before {
  background-color: rgba(39, 174, 96, 1); 
}

.maintenance::before {
  background-color: #FAA036; 
}

.disconnected::before {
  background-color: #FF0000; 
}

.card-container.expanded {
  height: 400px;
  z-index: 10;
}

.additional-info {
  padding: 10px;
  display: flex;
  align-items: flex-start;
  gap: 10px;
  justify-content: space-between;
  z-index: 10;
  margin-top: -15px;
}

.drop-icon {
  position: absolute;
  /* bottom: 3px;
  right: 210px; */
  /* margin-top: 25px; */
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
}

.drop-icon.rotated img {
  transform: rotate(180deg);
}

.chart-percentage {
  position: absolute;
  top: 35%;
  left: 40%;
  font-size: 25px;
  font-weight: 600;
  color: #3E3E3E;
  pointer-events: none;
}

.product-metrics-section,
.cycle-info-section {
  width: 48%;
  border-right: 1px solid #d9d9d9;
  padding-right: 10px;
}

.cycle-info-section {
  border-right: none;
}

h3 {
  font-size: 14px;
  
  color: #3e3e3e;
  margin-bottom: 10px;
  text-align: left;
  border-bottom: 1px solid #D9D9D9;
  padding-bottom: 5px;
}

.metrics {
  display: flex;
  flex-direction: column;
  gap: 1px;
}

.metric-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  font-size: 16px;
  color: #3E3E3E;
  font-weight: 400;
  padding: 2px 0;
  border-bottom: 1px solid #D9D9D9;
}

.metric-row:last-child {
  border-bottom: 1px solid #D9D9D9;
}

.metric-label {
  font-weight: normal;
}

.metric-value {
  font-weight: bold;
  text-align: center;
  margin-right: 60px;
}

.more-button {
  margin-top: 10px;
  text-align: center;
}

.more-button button {
  background-color: transparent;
  color: #275AFF;
  font-size: 14px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  padding: 0;
}

.more-button button:hover {
  color: #275AFF;
}

.cycle-info-section .metric-row {
  border-bottom: none;
}